.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.mid {
  text-align: center;
}

.rod {
  color: red;
  background-color: black;
  border-radius: 10px;
}

.navbar {
  justify-content: space-between;
  display: flex;
  align-items: center;

}




.rock {
  display: flex;
  gap: 3px;
}

.rock li {
  list-style: none;
  font-weight: 400;
  background-color: black;
  color: white;
  border-radius: 5px;
  padding: 5px;
}

.rock li:hover {
  cursor: pointer;
  color: red;
}

/* reach js part */
.bd .left {
  /* border: 2px solid red; */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20%;
}

.right i {
  margin: 10px;
}

.bd .right {
  /* border: 2px solid green; */
  width: 40vw;
  display: flex;
  flex-direction: column;
  gap: 80px;
  align-items: center;
  font-size: 20px;
  align-items: center;
  justify-content: center;
}

.bd {
  display: flex;
}

.cont h3 {
  text-align: center;
  color: white;
}

.right a {
  color: white;
}


.cont {
  height: 500px;
  background-color: red;
  /* For browsers that do not support gradients */
  background-image: linear-gradient(black, rgb(59, 58, 58));
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }

}

@media screen and (max-width:430px) {

  .navbar {
    display: flex;
    flex-direction: column;
    width: 100%;
    text-align: center;
  }

  .left {
    margin: 10px;
  }

  .bd {
    flex-direction: column;
  }

  .bd .right {
    width: 100vw;
  }

  .cont {
    border-radius: 20px;
    margin: 10px;
    height: 400px;
  }

  .bd iframe {
    margin-top: 80px;
    height: 400px;
  }
  .bd .left {
    margin: auto;
  }
  .left img{
    width: 100%;
    height: 30%;
    border-radius: 15px;
  }
  .left img {
    width: 20%;
  }
}
.hline{
  color: yellow;
  margin: 5px;
  background-color: red;
  border-radius: 20px;
  padding: 5px;
}
.rod{
  background-color: rgb(57, 57, 224);
  color: white;
  font-weight: 500;
  font-size: 15px;
  text-align: center;
}
.card img{
  margin-top: 10px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* map css */
.mapouter {
  position: relative;
  text-align: right;
  width: 600px;
  height: 400px;
}

.gmap_canvas {
  overflow: hidden;
  background: none !important;
  width: 600px;
  height: 400px;
}


.gmap_iframe {
  width: 600px !important;
  height: 400px !important;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}